<template>
	<div>
		<div class="back-title"><span>账号功能</span></div>
		<div class="pagebox" style="margin: 10px 0;">
			<Table :columns="columns1" :data="tableData3" :disabled-hover="true"></Table>
		</div>
	</div>
</template>

<script>
 export default {
   name: 'notice',
   data () {
     return {
       columns1: [
         {
           title: '功能',
           key: 'functionname',
           width: '100',
           className: 'noticebox'
         },
         {
           title: '状态',
           key: 'functionstast',
           className: 'noticebox',
           width: '100',
           render: (h, params) => {
             const row = params.row
             const color = row.functionstatus === 1 ? 'blue' : row.functionstatus === 2 ? 'green' : 'red'
             const text = row.functionstatus === 1 ? '已获取' : row.functionstatus === 2 ? '可获取' : '未获取'
             const sta = row.functionstatus === 1 ? 'true' : row.functionstatus === 2 ? 'true' : 'false'
             return h('Tag', {
               props: {
                 type: 'border',
                 color: color,
                 checkable: sta
               }
             }, text)
           }
         },
         {
           title: '申请条件',
           key: 'condition',
           className: 'noticebox'
         },
         {
           title: '功能说明',
           key: 'functionurl',
           className: 'noticebox',
           width: '100',
           render: (h, params) => {
             return h('a', {
               attrs: {
                 href: params.row.functionurl,
                 target: '_blank'
               }
             }, '功能介绍')
           }
         }
       ],
       tableData3: [
         {
           functionname: '自营广告',
           functionstatus: 1,
           condition: '符合条件的头条号可以申请开通自营广告',
           functionurl: 'https://www.toutiao.com/i6487462122262364685/'
         },
         {
           functionname: '原创标签',
           functionstatus: 2,
           condition: '优质原创头条号可申请开通原创标签。',
           functionurl: 'https://www.toutiao.com/i6487462122262364685/'
         },
         {
           functionname: '千人万元',
           functionstatus: 0,
           condition: '开通原创标签的个人帐号才可申请。',
           functionurl: 'https://www.toutiao.com/i6487462122262364685/'
         },
         {
           functionname: '外图封面',
           functionstatus: 0,
           condition: '累计粉丝数2000以上；头条号指数600以上。',
           functionurl: 'https://www.toutiao.com/i6487462122262364685/'
         }
       ]}
   },
   methods: {
     getdata (name) {
       console.log(name)
     }
   }
 }
</script>

<style>
</style>


